import { Injectable } from '@angular/core';
import { FormControl } from '@angular/forms';

@Injectable({
  providedIn: 'root'
})
export class FormErrorService {

  constructor() { }
  emailValidator(control: FormControl) {
    let email = control.value;
    let EMAIL_REGEXP = /^[A-Za-z0-9 _!#$%&'*+\/=?^_`{|}~.-]+@[a-z]([a-z]*[a-z]\.[a-z]([a-z]*[a-z])?)*$/g;

    if (email && email != "" && (email.length <= 5 || !EMAIL_REGEXP.test(email))) {
        return { "email": { "message": "Please enter valid email address" } };
    }
    return null;
}
// /^([a-zA-Z]([-.\w]*[0-9a-zA-Z])*@([0-9a-zA-Z][-\w]*[0-9a-zA-Z])(\.([a-zA-Z]{2,4})){0,1}(\.[a-zA-Z]{2,4}))$/

nameValidator(control: FormControl) {
    let name = control.value;
    let NAME_REGEXP = /^[a-zA-Z \.]*$/g;

    if (name && name != "" && (name.length < 2 || !NAME_REGEXP.test(name))) {
        return {
            "name": { "message": "Please enter valid name" }
        };
    }
    return null;
}

stringValidator(control: FormControl) {
    let name = control.value;
    let NAME_REGEXP = /^[a-zA-Z ]*$/g;

    if (name && name != "" && !NAME_REGEXP.test(name)) {
        return {
            "name": { "message": "In valid entry" }
        };
    }
    return null;
}

numberValidator(control: FormControl) {
    let number = control.value;
    let NUMBER_REGEXP = /^[0-9]*$/g;
    if (number && number != "" && !NUMBER_REGEXP.test(number)) {
        return { "number": { "message": "Please enter valid number" } };
    }
    return null;
}

mobileValidator(control: FormControl) {
    let mobile = control.value;
    let MOBILE_REGEXP = /^(\+[9][1][- ]?)?[7-9][0-9]{9}$/g;
    // let MOBILE_REGEXP= /^(\([0-9]{3}\) |[0-9]{3}-)[0-9]{3}-[0-9]{4}$/g
    // let MOBILE_REGEXP=/^\([0-9]{3}\)[0-9]{3}-[0-9]{4}$/g

    if (mobile && mobile != "" && !MOBILE_REGEXP.test(mobile)) {
        return { "mobile": { "message": "Please enter valid mobile number" } };
    }
    return null;
}


mobileValidator1(control: FormControl) {
    let mobile = control.value;
    let MOBILE_REGEXP = /^(\+  [- ]?)?[1-9][0-9]{9}$/g;
    // let MOBILE_REGEXP= /^(\([0-9]{3}\) |[0-9]{3}-)[0-9]{3}-[0-9]{4}$/g
    // let MOBILE_REGEXP=/^\([0-9]{3}\)[0-9]{3}-[0-9]{4}$/g

    if (mobile && mobile != "" && !MOBILE_REGEXP.test(mobile)) {
        return { "mobile": { "message": "Please enter valid mobile number" } };
    }
    return null;
}



shouldNotContainSpecialCharactersAndAlphabets(control: FormControl) {
    let char = control.value;
    let char_REGEXP = /^[a-zA-Z\[\]!@#$%^&*\(\)_=~`\.?\/\\<>,"':;{}|]/;

    if (char && char != "" && char_REGEXP.test(char)) {
        return { "error": { "message": "Should not contain special character or alphabets" } };
    }
    return null;
}

priceValidator(control: FormControl) {
    let price = control.value;
    let PRICE_REGEXP = /^[0-9]+([\.][0-9]{1,3})?$/g;
    if (price && price != "" && !PRICE_REGEXP.test(price)) {
        return { "price": { "message": "Please enter valid amount" } };
    }
    return null;
}

yearValidator(control: FormControl) {
    let year = control.value;
    let YEAR_REGEXP = /^[1-2][0-9][0-9][0-9]$/g;
    // let YEAR_REGEXP = /^([1][9][7-9][0-9])?|([2][0][0-1][0-9])?$/g;

    if (year && year != "" && !YEAR_REGEXP.test(year)) {
        return { "year": { "message": "Please enter valid year" } };
    }
    return null;
}

fullDateValidator(control: FormControl) {
    let fullDate = control.value;
    let FULL_DATE_REGEXP = /^[0-3]?[0-9][ -\.\/][0-1]?[0-9][ -\.\/][1-2][0-9][0-9][0-9]$/g;

    if (fullDate && fullDate != "" && !FULL_DATE_REGEXP.test(fullDate)) {
        return { "fulldate": { "message": "Please enter valid date" } };
    }
    return null;
}

urlValidator(control: FormControl) {
    let url = control.value;
    let URL_REGEXP = /(http(s)?:\/\/.)?(www\.)?[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)/g;

    if (url && url != "" && !URL_REGEXP.test(url)) {
        return { "url": { "message": "Please enter valid URL" } };
    }
    return null;
}

noSpecialCharacter(control: FormControl) {
    let error = control.value;
    let SPECIAL_CHARACTER_REGEXP = /[ !@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?]/;
    if (error && error != "" && SPECIAL_CHARACTER_REGEXP.test(error)) {
        return { "error": { "message": "Please enter valid URL" } };
    }
    return null;
}

cardExpiryMonthValidator(control: FormControl) {
    let month = control.value;
    let MONTH_REGEXP = /^1[0-2]|(0[1-9])$/g;

    if (month && typeof month != "number" && !MONTH_REGEXP.test(String(month))) {
        return { "expiry_month": { "message": "Please enter valid expiry month" } };
    }
    return null;
}

cardExpiryYearValidator(control: FormControl) {
    let year = control.value;
    let date = Number(String((new Date()).getFullYear()).substr(2));
    if (year && typeof year != "number" && date > year) {
        return { "expiry_year": { "message": "Please enter valid expiry year" } };
    }
    return null;
}

usZipcodeValidator(control: FormControl) {
    let zipcode = control.value;
    let ZIP_REGEX = /^(?!0{5})(\d{5})(?!-?0{4})(|-\d{4})?$/g;
    if (zipcode && typeof zipcode != "number" && !ZIP_REGEX.test(String(zipcode))) {
        return { "zipcode": { "message": "Please enter valid Zipcode" } };
    }
    return null;
}
}
