import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AdminLoginService } from './admin-login.service';
import { FormErrorService } from '../shared/service/form-error.service';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { NotificationService } from '../shared/service/notifiation.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-login-admin',
  templateUrl: './login-admin.component.html',
  styleUrls: ['./login-admin.component.css']
})
export class LoginAdminComponent implements OnInit {
  loginForm: FormGroup;
  constructor(
    private admin: AdminLoginService,
    private tnFormErrorService: FormErrorService,
    private router: Router,
    private spinner: NgxSpinnerService,
    private notification: NotificationService,
    private toaster: ToastrService
  ) { }
  ngOnInit() {
    this.loginForm = new FormGroup({
      email: new FormControl('', [
        Validators.required,
        this.tnFormErrorService.emailValidator
      ]),
      password: new FormControl('', [
        Validators.required
      ])
    });
  }


  login() {
    this.admin.adminLogin(this.loginForm.value).subscribe(res => {
      this.spinner.show();
      console.log('response', res);
      if (res.code === 200) {
        this.spinner.hide();
        this.toaster.success('Login Successful');
        sessionStorage.setItem('mm_da', res.data.token);
        this.router.navigate(['admin/dashboard']);
      } else if (res.code === 500) {
        this.spinner.hide();
        // this.notification.infoToast(res.message);
        this.toaster.success(res.message);
      } else {
        this.spinner.hide();
        this.toaster.info(res.message);
        // this.notification.infoToast(res.message);
      }
    });
  }
}
