import { Pipe, PipeTransform } from '@angular/core';
import * as momentTz from 'moment-timezone';

@Pipe({
  name: 'timezonewithtimestamp'
})
export class TimezonewithtimestampPipe implements PipeTransform {

  transform(value: any, timezone?: any, format?: string): any {
    console.log(value, 'value')

    let value1 = Math.trunc(value)
    console.log(value1, 'value1')
    let date = new Date(value1)
    console.log('date', date)
    let toStringValue = date.toString()
    console.log('toStringValue', toStringValue)
    return momentTz.tz(toStringValue, timezone).format(format)
  }

}
