import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AdminService } from '../admin/admin.service';
import { AdminLoginService } from '../login-admin/admin-login.service';
import { ActivatedRoute } from '@angular/router';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-forgotpassword',
  templateUrl: './forgotpassword.component.html',
  styleUrls: ['./forgotpassword.component.css']
})
export class ForgotpasswordComponent implements OnInit {
  forgotpasswordForm: FormGroup
  constructor(private admin: AdminLoginService,
    private route: ActivatedRoute, 
  private toastr:ToastrService) { }

  ngOnInit() {
    this.forgotpasswordForm = new FormGroup({
      password: new FormControl('', [
        Validators.required,
        // this.tnFormErrorService.stringValidator
      ]),
      confirmPassword: new FormControl('', [
        Validators.required,
        // this.tnFormErrorService.priceValidator
      ])

    });
this.getEmail()

  }


  getEmail() {
    this.route.params.subscribe(params => {
      this.admin.emailforpassword(params['id']).subscribe(res => {
        console.log('resss-----------------', res)
      })
    })
  }

  submit() {
    if (this.forgotpasswordForm.valid) {
      if (this.forgotpasswordForm.value.password == this.forgotpasswordForm.value.confirmPassword) {
        this.route.params.subscribe(params => {
          this.admin.forgotpassword(this.forgotpasswordForm.value, params['id']).subscribe(res => {
            console.log(res)
            if (res.code == 200) {
              // alert('Reset password successful')
              this.toastr.success("Reset password successful")
            }
          })
        })
      } else {
        this.toastr.info("Password and Confirm password did not match")
      }
    } else {

    }
  }


}




