import { Injectable } from '@angular/core';
import * as FileSaver from 'file-saver';
import * as XLSX from 'xlsx';

const EXCEL_TYPE = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
const EXCEL_EXTENSION = '.xlsx';
@Injectable({
  providedIn: 'root'
})
export class ExportXlxsService {

  constructor() { }
  public exportAsExcelFile(json: any[], excelFileName: string): void {
    
    const worksheet: XLSX.WorkSheet = XLSX.utils.json_to_sheet(json);
    console.log('worksheet',worksheet);
    const workbook: XLSX.WorkBook = { Sheets: { 'data': worksheet }, SheetNames: ['data'] };
    const excelBuffer: any = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
    console.log(excelBuffer,"excelBuffer")
    //const excelBuffer: any = XLSX.write(workbook, { bookType: 'xlsx', type: 'buffer' });
    this.saveAsExcelFile(excelBuffer, excelFileName);
  }
  
	public exportAsExcelFileAsWorkbook(json: any[],json1: any[],json2: any[],json3: any[],json4: any[],json5: any[],json6: any[],json7: any[], excelFileName: string): void {
		
		


		const ws = XLSX.utils.aoa_to_sheet(json);
		const ws1 = XLSX.utils.aoa_to_sheet(json1);
		const ws2 = XLSX.utils.aoa_to_sheet(json2);
		const ws3 = XLSX.utils.aoa_to_sheet(json3);
		const ws4 = XLSX.utils.aoa_to_sheet(json4);
		const ws5 = XLSX.utils.aoa_to_sheet(json5);
		const ws6 = XLSX.utils.aoa_to_sheet(json6);
		const ws7 = XLSX.utils.aoa_to_sheet(json7);
		const wb = XLSX.utils.book_new();
		XLSX.utils.book_append_sheet(wb, ws,"Matching Reports");
		XLSX.utils.book_append_sheet(wb, ws1,"Actual Date Users");
		XLSX.utils.book_append_sheet(wb, ws2,"Sorted Questionnier User");
		XLSX.utils.book_append_sheet(wb, ws3,"Sorted Nomatchpass Users");
		XLSX.utils.book_append_sheet(wb, ws4,"Sorted Lists Users");
		XLSX.utils.book_append_sheet(wb, ws5,"No Matchpass Users");
		XLSX.utils.book_append_sheet(wb, ws6,"Call Drop Users");
		XLSX.utils.book_append_sheet(wb, ws7,"Accept Reject Statuses");
		const excelBuffer: any = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });
		this.saveAsExcelFile(excelBuffer, excelFileName);
	} 
  

  private saveAsExcelFile(buffer: any, fileName: string): void {
    const data: Blob = new Blob([buffer], {
      type: EXCEL_TYPE
    });
    FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + EXCEL_EXTENSION);
  }
}
