import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpRequest, HttpHandler, HttpEvent, HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs';
import { tap} from 'rxjs/operators';
import { StringConst } from '../utils/string-constants';

@Injectable({
  providedIn: 'root'
})
export class InterceptorUService implements HttpInterceptor {

  constructor() { }

// intercept request and add token
intercept(request: HttpRequest<any>, next: HttpHandler):Observable<HttpEvent<any>> {

    request = request.clone({
      setHeaders: {
      Authorization: `Bearer ${sessionStorage.getItem(StringConst.ADMIN_LOCAL_STORAGE_NAME)}`
      }
    });



  return next.handle(request)
  .pipe(
      tap(event => {
        console.log('eteterterttrtrtrtertertrtert')
        if (event instanceof HttpResponse) {
           
         
        }
      }, error => {
       // http response status code

          console.error(error.status);
          console.error(error.message);


      })
    )
};

}