import { Injectable } from '@angular/core';
import swal from 'sweetalert2';
// import { url } from 'inspector';

@Injectable()
export class NotificationService {
  constructor() {
  }
    confirm(text, title = 'Are you sure?', confirmtype?: any, confirmButtonText = 'Proceed') {
    let obj = {
      title: title,
      text: text,
      type: confirmtype,
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: confirmButtonText,

    }
    return swal(obj);
  } 
  notificationSent(text,title='Notification sent!!!'){
    let obj={
   
        title: text,
        text:title,
        icon: "success",
      
    }
    return swal(obj)
  }
    private toast(title, type) {
    swal({
      position: 'top',
      type: type,
      title: title,
      showConfirmButton: false,
      toast: true,
      timer: 3000,

    });
  
}

  infoToast(title) {
    this.toast(title, 'info');
  }

  errorToast(title) {
    this.toast(title, 'error');
  }

  warningToast(title) {
    this.toast(title, 'warning');
  }

  questionToast(title) {
    this.toast(title, 'question');
  }

  successToast(title) {
    this.toast(title, 'success');
  }
}

