import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, Observer } from 'rxjs';
import { ElesAppConst } from '../shared/utils/eles-app-constant';
import { StringConst } from '../shared/utils/string-constants';

@Injectable({
  providedIn: 'root'
})
export class AdminLoginService {

  constructor(private http: HttpClient) { }


  adminLogin(data): Observable<any> {
    if (data) {
      return this.http.post(ElesAppConst.ADMIN_LOGIN, data);
    }  else {
      return Observable.create((observer: Observer<any>) => { return observer.error(StringConst.INVALID_INFORMATION); })
    }
  }
  forgotpassword(data, id): Observable<any> {
    if (data) {
      return this.http.post(ElesAppConst.FORGOT_PASSWORD + id, data);
    } else {
      return Observable.create((observer: Observer<any>) => { return observer.error(StringConst.INVALID_INFORMATION); })
    }
  }
  emailforpassword(data) {
    if (data) {
      console.log('fsdfdfd', data);
      const dt1 = {
        id: data
      }
      return this.http.post(ElesAppConst.EMAIL_FOR_PASSWORD, dt1);
    } else {
   return Observable.create((observer: Observer<any>) => { return observer.error(StringConst.INVALID_INFORMATION); })
    }
  }



  public getTokenA(): string {
    return sessionStorage.getItem(StringConst.ADMIN_LOCAL_STORAGE_NAME);
  }
  
  isLoggedInA() {
    return this.getTokenA() !== null
  }
  
  // public getToken(): string {
  //   return localStorage.getItem(StringConst.USER_LOCAL_STORAGE_NAME);
  // }
  
  // isLoggedIn() {
  //   return this.getToken() !== null
  // }
}
