import { Pipe, PipeTransform } from '@angular/core';
import * as momentTz from 'moment-timezone';

@Pipe({
  name: 'dateWithTimezone'
})
export class DateWithTimezonePipe implements PipeTransform {

  transform(value: any, timezone?: any, format?: string): string {

    let toStringValue=value.toString();
    // console.log('toStringValue',toStringValue)
    return momentTz.tz(toStringValue, timezone).format(format);
  }

}





